<template>
  <el-row class="warp" style="padding:20px 0 0 20px;">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
         <el-form-item>
          <el-input v-model="filters.name" placeholder="ID//" style="min-width: 240px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" size="medium"></el-button>
          <el-button @click="clearss"  size="medium"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="applyData" highlight-current-row style="width: 95%;" size="small" stripe border>

      <el-table-column prop="user_id" label="ID" width="100" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" width="min-200" align="center">
      </el-table-column>

      <el-table-column prop="phone" label="" width="min-200" align="center">
      </el-table-column>

      <el-table-column prop="account" label="" width="min-200" align="center">
      </el-table-column>

      <el-table-column prop="bank" label="" width="min-200" align="center">
      </el-table-column>

      <el-table-column prop="created_at" label="" width="min-200" align="center">
      </el-table-column>

<!--       <el-table-column prop="updated_at" label="" min-width="190" align="center">
</el-table-column> -->

<el-table-column prop="id" label="" width="200" align="center" v-show="checkper1">
  <template slot-scope="scope">
    <el-tag size="mini" type="success" v-show="scope.row.state==2 ? true : false"></el-tag>
    <el-tag size="mini" type="danger" v-show="scope.row.state==3 ? true : false"></el-tag>
    <el-button size="mini" v-show="scope.row.state==1 ? true : false" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
    <el-button size="mini" v-show="scope.row.state==1? true : false" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
  </template>
</el-table-column>

</el-table>
<el-pagination style="float:left;margin:20px 0 0 0px;" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="limit" @current-change="handleCurrentChange" @size-change="handleSizeChange" layout="total,sizes, prev, pager, next, jumper" :total="count" prev-text="" next-text="">
</el-pagination>
</el-col>

</el-col>

<!--  -->
<el-col>
  <el-dialog title="" :visible.sync="dialogDelVisible" width="30%">
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogDelVisible  = false"> </el-button>
      <el-button type="primary" @click="submitdel()"> </el-button>
    </div>
  </el-dialog>
</el-col>

</el-row>
</template>


<script>

  import { aapplyGet } from '../../api/api';
  import { aapplyPass } from '../../api/api';
  import { aapplyReject } from '../../api/api';
  import { Message } from 'element-ui';

  export default {
    data() {
      return {

        applyData: [],
        currentid:'',
        currentPage: 1,
        count:0,
        limit:10,
        dialogDelVisible:false,
        filters: {
          name: ''
        },
        dialogSeeVisible: false,
        checkper1:false,
      }
    },
    methods: {

      checkPer(){
        var per = sessionStorage.getItem('permissions');

        if(per.indexOf('proxyApplyCheck')>-1){
          this.checkper1=true;
        }

      },



      //
      getlist: function () {
        var allParams = '?page='+ this.currentPage + '&limit=' + this.limit + '&search=' + this.filters.name;
        aapplyGet(allParams).then((res) => {
          this.applyData=res.data.data;
          this.count=res.data.count;
        });
      },

      search:function(){
        if(this.filters.name==''){
          this.$message.error('');
          return;
        }else{
          this.getlist();
        }
      },

      clearss:function(){
        this.filters.name='';
        this.getlist();
      },

      //
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getlist();
      },

      //
      handleSizeChange(val){
        this.limit = val;
        this.getlist();
      },


      handleEdit(index, row) {
        // console.log(row.id)
        var allParams = '?id='+ row.id;
        aapplyPass(allParams).then((res) => {
          console.log(res)
          Message({
            message: "",
            type: 'success'
          });
          this.getlist();
        });
      },

      handleDelete(index, row) {
        this.currentid= row.id
        this.dialogDelVisible = true;
      },

      submitdel(){
        var allParams = '?id='+ this.currentid;
        aapplyReject(allParams).then((res) => {
          console.log(res)
          Message({
            message: "",
            type: 'success'
          });
          this.dialogDelVisible = false;
          this.getlist();
        });
      },

    },

    mounted() {
      this.getlist();
      this.checkPer();
    }
  }
</script>

<style scoped>

</style>



